@charset "UTF-8";
/* Blue theme - #00acee; #15202a; */
:root {
  --accent1: rgb(21, 32, 42);
  --accent1-5: rgb(0, 172, 238);
  --accent2: rgb(247, 247, 247);
  --accent3:var(--accent1);
  --text1-accent: rgb(74, 74, 74);
  --text2-accent:rgba(183, 183, 183, 0.5);
  --text3-accent: rgb(183, 183, 183);
  --textbtn-accent: #ffffff;
  --topbar-background: var(--accent1);
  --screen: #202030;
}
body {
  box-sizing: content-box;
  overflow-x: hidden;
}
.fulldimention {
  height: 100vh !important;
  width: 100vw !important;
}
/* Global margin div*/
.MargDiv {
  height: 4.8em;
  width: 100%;
}
/* Card css for global use*/
.maintitle {
  color: var(--text1-accent);
  font-weight: 400;
  font-family: Merriweather, Georgia, serif;
  font-size: 2.45rem;
  line-height: 1;
  text-align: center;
}
.h01btn {
  position: relative;
  left: -3.5em;
}
.divcenter {
  margin: auto !important;
  display: table;
}
.bcktitle {
  background-color: var(--accent1);
}
.mainresolution {
  height: 100vh !important;
  width: 100vw !important;
}
.secondbg_color {
  background-color: var(--accent2);
}
.cotitle {
  color: var(--text1-accent);
  font-family: var(--fontfamily);
  font-weight: 400;
  text-align: center;
  font-size: 1rem;
  line-height: 4em;
}
.cardouter {
  box-sizing: border-box;
  display: flex;
  width: 320px;
  align-items: center;
  background-color: var(--accent1);
  box-shadow: var(--accent1) 0px 47px 46px -27px;
  font-family: Lato, "Helvetica Neue", Arial, sans-serif;
  font-size: 1.6rem;
  line-height: 1.3;
  font-weight: 400;
  flex-flow: column;
  border-radius: 12px;
  padding: 29px 38px;
  margin: 20px;
}
.cardtitle {
  position: relative;
  font-weight: 400;
  line-height: 1.9rem;
  white-space: pre;
  text-align: center;
  font-family: 30px Lato, "Helvetica Neue", Arial, sans-serif;
  color: var(--text2-accent);
}
.cardtext {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.3;
  color: #b7b7b7;
}
.cardprice {
  color: var(--accent2);
  font-family: Merriweather, Georgia, serif;
  font-size: 5.1rem;
  line-height: 7.7rem;
  font-weight: 500;
}
.whitetext {
  color: white;
}
.iconimg {
  height: 6em;
  width: 6em;
  z-index: 1;
}
/* Project Theme */
.buttonstyle {
  opacity: 1;
  font-family: Lato, "Helvetica Neue", Arial, sans-serif !important;
  line-height: 1.3 !important;
  font-weight: 700 !important;
  text-align: center !important;
  cursor: pointer !important;
  text-transform: uppercase !important;
  position: relative !important;
  white-space: nowrap !important;
  font-size: 14px !important;
  outline: none !important;
  text-decoration: none !important;
  padding: 13px 60px 13px 60px !important;
  border-radius: 5px !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: transparent !important;
  border-image: initial !important;
  color: var(--textbtn-accent) !important;
  transition: all 0.3s ease 0s, transform 0.5s ease 0s !important;
  background-color: var(--accent1-5) !important;
  box-shadow: var(--accent1-5) !important;
  display: table !important;
  margin: auto !important;
}
.buttonstyle:hover {
  transform: translateY(-0.25em) !important;
  filter: brightness(85%);
}
.disablestyle {
  opacity: 0.1 !important;
  font-family: Lato, "Helvetica Neue", Arial, sans-serif !important;
  line-height: 1.3 !important;
  font-weight: 700 !important;
  text-align: center !important;
  cursor: pointer !important;
  text-transform: uppercase !important;
  position: relative !important;
  white-space: nowrap !important;
  font-size: 14px !important;
  outline: none !important;
  text-decoration: none !important;
  padding: 13px 60px 13px 60px !important;
  border-radius: 5px !important;
  border-width: 1px !important;
  border-style: solid !important;
  border-color: transparent !important;
  border-image: initial !important;
  color: var(--textbtn-accent) !important;
  transition: all 0.3s ease 0s, transform 0.5s ease 0s !important;
  background-color: var(--accent1) !important;
  box-shadow: var(--accent1) !important;
  display: table !important;
  margin: auto !important;
}
.hrline {
  background-color: var(--accent1) !important;
  height: 1.3px;
}
.alertspan {
  color: var(--accent1);
}
input[type=radio]:after {
  width: 25px !important;
  height: 25px !important;
  border-radius: 15px !important;
  top: -2px !important;
  left: -1px !important;
  position: relative !important;
  background-color: white !important;
  content: "";
  display: inline-block !important;
  visibility: visible !important;
  border: 1px solid gray !important;
  padding: 0.3em;
}
input[type=radio]:checked:after {
  width: 25px !important;
  height: 25px !important;
  border-radius: 15px !important;
  top: -2px !important;
  left: -1px !important;
  position: relative !important;
  background-color: var(--accent1) !important;
  display: inline-block !important;
  visibility: visible !important;
  border: 1px solid gray !important;
  padding: 0.3em;
}
input[type=checkbox]:after {
  width: 1.25em !important;
  height: 1.25em !important;
  left: -0.35em !important;
  position: relative !important;
  background-color: white !important;
  content: "";
  display: inline-block !important;
  visibility: visible !important;
  border: 1px solid gray !important;
  text-align: center;
  font-size: 1.25em;
  font-family: bold;
  color: var(--accent1) !important;
}
input[type=checkbox]:checked:after {
  content: "✔";
}
.modelOpenButt {
  background-color: var(--accent1);
  box-shadow: rgba(15, 15, 15, 0.699) 0px 20px 40px -10px;
  position: absolute;
  top: 0.7em;
  left: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  padding: 0;
  font: 700 22px/30px "Lato", "Helvetica Neue", Arial, sans-serif;
  line-height: 30px;
  cursor: pointer;
}
.pac-container {
  display: none;
}
input[type=number] {
  -moz-appearance: textfield;
}
.form-check-inline {
  margin: 0 !important;
}
.form-check-inline .form-check-input {
  margin: 0 !important;
}